  border-bottom: 1px solid #f3f3f3;
  img {
    width: 100%;
    vertical-align: middel;
  }
  .go_back {
    position: fixed;
    z-index: 20;
    width: 1.28rem;
    left: $p10;
    top: $p10;
  }
}

.name_price {
  padding: $p10 $p15;
  .name {
    @include sc($p14, #333);
    padding-bottom: 4px;
  }
  .price {
    @include sc($p12, #fca90f);
  }
}

.small_title {
